<template>
    <v-toolbar class="g-toolbar">
      <v-toolbar-title>LADDER</v-toolbar-title>
      <v-flex xs2 class="g-search-wrap">
        <v-text-field
          class="g-search-field"
          name="input-1"
          rows=1
          row-height=10
          prepend-icon="search"
          hide-details="true"
        />
      </v-flex>
      <v-spacer/>
      <v-toolbar-items class="hidden-sm-and-down">
        <v-flex
          xs12
          sm6
          md8
          text-xs-center
          layout
          align-center
          justify-center
        >
          <v-avatar
            :tile=false
            :size=40
            color="grey lighten-4"
          >
            <img src="../assets/logo.png" alt="avatar">
          </v-avatar>
        </v-flex>
      </v-toolbar-items>
    </v-toolbar>
</template>

<script>
    export default {
        name: "toolbar-component"
    }
</script>

<style scoped lang="sass">
    .g-toolbar
        background: #4694F2
    .g-search-wrap
        padding: 10px 0
        margin: 0 0 0 30px
    .g-search-field
        padding: 0
        background: #fff
        border-radius: 30px
</style>


